.main {
  padding-right: 10px;
  flex: 1;
  overflow-y: scroll;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: minmax(min-content, max-content);
  gap: 10px;
  align-items: center;
}

.main-item {
  padding: 10px;
  border-radius: 8px;
  background-color: rgb(240, 240, 240);
  transition: background-color 0.1s;
  height: 100%;
  box-sizing: border-box;
}

.main-item:hover {
  background-color: rgb(225, 225, 225);
}

.main-image-div {
  display: inline-block;
  width: 76px;
}

.main-image {
  height: 64px;
  width: 64px;
}

.main-content {
  display: inline-block;
  vertical-align: top;
}

.main-name {
  font-size: 16px;
  font-weight: bold;
}

.main-text {
  font-size: 16px;
}