.header {
  padding: 12px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: rgb(210, 232, 255);
  position: relative;
  z-index: 1;
}

.header-logo {
  vertical-align: middle;
  height: 40px;
  width: 40px;
}

.header-title {
  display: inline-block;
  vertical-align: middle;
  margin-left: 10px;
  font-size: 24px;
  font-weight: bold;
  color: #444;
}