.aside {
  width: 150px;
  padding: 15px;
  /* border-left: 1px solid #ddd; */
}

.aside-item {
  height: 75px;
}

.aside-label {
  font-size: 16px
}

.aside-input-item {
  width: 100%;
  height: 28px;
  padding-left: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  transition: border 0.2s;
}

.aside-input-item:focus {
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(126, 158, 255);
  box-sizing: border-box;
}

.aside-option {
  background-color: #eee;
  color: #36f;
  font-weight: bold;
  font-size: 15px;
}